 
.menu {
  position: absolute;
  display: flex;
    background-color: #333;
    padding: 10px 0;
    top: 0%;
    left: 0px;
    width: 100%;
    height: 80px;
    z-index: 122123412;
  }
  
   
  .menu ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
  }
  
 
  .menu ul li {
    margin: 0 15px;
  }
  
 
  .menu ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
    transition: color 0.3s ease;
  }
  
 
  .menu ul li a:hover {
    color: #66aebe;
  }
  
  @media (max-width: 768px) {
    .menu {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; 
      height: auto;
    }
  }