 
.formulario-pago {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
  }
  
  .formulario-pago h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .formulario-pago form {
    display: flex;
    flex-direction: column;
  }
  
  .formulario-pago label {
    margin-bottom: 10px;
  }
  
  .formulario-pago input {
    padding: 8px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
  }
  
  .formulario-pago button {
    padding: 10px;
    margin-top: 20px;
    border: none;
    border-radius: 3px;
    background-color: #007bff;
    color: #fff;
    cursor: pointer;
  }
  