.grid-container {
    position: relative;
    width: 100%;
    height: 80vh;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(22rem, 1fr));  
    grid-auto-rows: 22rem;  
    gap: 10px; 
 
}

.item {
    background-color: rgb(0, 69, 92);
 
}

 
