 
.noticias-container {
    margin: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
  }
  
 
  .noticias-container h2 {
    margin-bottom: 15px;
    font-size: 24px;
  }
 
  .noticias-list {
    display: grid;
    grid-gap: 20px;
  }
  
 
  .noticia {
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #fff;
  }
  
 
  .noticia h3 {
    margin-bottom: 10px;
    font-size: 18px;
  }
  
 
  .noticia p {
    margin-bottom: 5px;
    font-size: 14px;
  }
  