.product-card {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 20px;
  margin: 20px;
  display: flex;
  align-items: center;
}

.product-card img {
  width: 150px;
  height: auto;
  margin-right: 20px;
}

.product-details {
  display: flex;
  flex-direction: column;
}

.product-details h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.product-details p {
  margin-bottom: 10px;
}

.product-details button {
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.product-details button:hover {
  background-color: #0056b3;
}

.product-card p {
  transition: transform 0.3s ease;
}

.product-card p:Hover {
  color: #0056b3;
  animation: animP 1s;
}

@keyframes animP {

  0% {
    transform: scale(7);
    animation-timing-function: ease-in;
    opacity: 0;
  }

  35% {
    transform: scale(1);
    animation-timing-function: ease-out;
    opacity: 1;
  }

  55% {
    transform: scale(1.5);
    animation-timing-function: ease-in;
  }

  70% {
    transform: scale(1);
    animation-timing-function: ease-out;
  }

  80% {
    transform: scale(1.24);
    animation-timing-function: ease-in;
  }

  90% {
    transform: scale(1);
    animation-timing-function: ease-out;
  }

  95% {
    transform: scale(1.04);
    animation-timing-function: ease-in;
  }

  100% {
    transform: scale(1);
    animation-timing-function: ease-out;
  }
}

.product-card img {
  filter: grayscale(100%);
  transition: filter 0.8s ease, transform 1.6s ease;

}

.product-card  img:hover {
  filter: grayscale(0%);
  transform: rotateY(180deg);
}
p{
  font-size: 11px;
}